import React, { useState } from "react";
import { Route, Redirect, Switch, BrowserRouter } from "react-router-dom";
import Blog from "./components/Blog";
import Musica from "./components/Musica";
import Index from "./components/Index";
import Article from "./components/article";

function App() {
  return (
    <React.Fragment>
      <BrowserRouter>
        <Switch>
          <Route path={process.env.PUBLIC_URL + "/musica"} component={Musica} />
          <Route path={process.env.PUBLIC_URL + "/como"} component={Article} />
          <Route path={process.env.PUBLIC_URL + "/blog"} component={Blog} />
          <Route path={process.env.PUBLIC_URL + "/"} component={Index} />
          <Redirect to={process.env.PUBLIC_URL + "/not-found"} />
        </Switch>
      </BrowserRouter>
    </React.Fragment>
  );
}

export default App;
